.main-sobre{
    display: flex;
    align-items: center;
    justify-content: center;

    .wrapper-sobre{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: 960px;

        >h1{
            font-family: 'Viga', Arial, Helvetica, sans-serif;
            font-weight: 400;
            font-size: 75px;
            line-height: 100px;
            color: #000;
            padding: 28px 0;
        }

        .text{
            width: 72%;
            font-family: 'Viga';
            font-size: 16px;
            font-weight: 400;
            line-height: 22px;
        }

        .social-media{
            display: flex;
            align-items: flex-end;
            border-radius: 14px;
            width: 377px;
            background: #FFABED;
            color: #FFF;
            font-family: 'Viga';
            margin-top: 12px;
        }
    }

    .wrapper-social{
        padding-top: 76px;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        margin-bottom: 30px;
        
    }

    
}